@font-face {
    font-weight: 100;
    font-family: 'Segoe UI';
    src: local('Segoe UI Light'), url('../fonts/Segoe-UI-Light.woff2') format('woff2'), url('../fonts/Segoe-UI-Light.woff') format('woff'), url('../fonts/Segoe-UI-Light.ttf') format('truetype');
}
@font-face {
    font-weight: 200;
    font-family: 'Segoe UI';
