/* Buttons */
.button {
    font-family: $fontFamily;
    outline: none !important;

    &.button-one {
        border: 2px solid $lightblue;
        border-radius: 6px;
        background-color: $lightblue;
        box-shadow: 0 3px 6px rgba(#000, 0.16);
        font-weight: $fontRegular;
        font-size: 1rem;
        color: #fff;
        padding: 10px 20px;

        @include transition(0.3s all);

        &.button-small {
            padding: 5px 10px;
        }

        &.button-dropdown {
            width: 100%;

            .icn {
                &.circle {
                    position: relative;
                    top: 2px;
                }

                &.arrow-down {
                    position: relative;
                    top: 2px;
                }
            }
        }

        &:hover {
            border-color: $blue;
            box-shadow: 0 3px 6px rgba(#000, 0.16), 0 0 10px 10px rgba(#FFF, 0.05) inset;

            @include transition(0.3s all);
        }
    }

    &.button-two {
        border: 2px solid $lightblue2;
        border-radius: 6px;
        background-color: $lightblue2;
        font-weight: $fontRegular;
        font-size: 1rem;
        color: #fff;
        padding: 10px 20px;

        @include transition(0.3s all);

        &.button-small {
            padding: 5px 10px;
        }

        &.button-dropdown {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }

        &:hover {
            border-color: $blue;
            box-shadow: 0 3px 6px rgba(#000, 0.16), 0 0 10px 10px rgba(#FFF, 0.05) inset;

            @include transition(0.3s all);
        }
    }
}

.dropdown.show {
    .button-one.button-dropdown {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }
}
.custom-dropdown {
    border: 2px solid $blue;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    background-color: $lightblue;
    box-shadow: 0 3px 6px rgba(#000, 0.16);
    margin-top: -2px;
    width: 100%;

    .dropdown-item {
        background: none;
        color: #9dafd6;

        @include transition(0.3s all);

        &:hover {
            background: none;
            color: #fd0;
            padding-left: 1.8rem;

            @include transition(0.3s all);
        }
    }
}

/* Inputs */
.form-group-one {
    border-radius: 6px;
    position: relative;
    width: 100%;
    height: 72px;
}

.label-one {
    font-size: 1.1rem;
    line-height: 72px;
    color: rgba(#fff, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    padding: 0 20px;
    height: 72px;
}

.input-one {
    border: 0;
    border-radius: 6px;
    background-color: #4765a3;
    font-size: 1.4rem;
    color: #FFF;
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 150px;
    width: 100%;
    height: 72px;
    outline: none;

    @include placeholder {
        color: #FFF;
    }
    @include transition(0.3s all);

    &:focus {
        background-color: $blue;

        @include transition(0.3s all);
    }
}

/* Checkboxes */
.custom-checkbox {
    .custom-control-label {
        font-size: 1.2rem;
        color: #9dafd6;
        padding-top: 8px;
        padding-left: 25px;
        cursor: pointer;

        &:before {
            border: 2px solid $blue;
            border-radius: 6px;
            background-color: #416bb5;
            box-shadow: 0 3px 6px rgba(#000, 0.16) !important;
            width: 40px;
            height: 40px;
        }

        &:after {
            width: 40px;
            height: 40px;
        }
    }
}
