.icn {
    display: inline-block;
    width: 15px;
    height: 15px;

    &.x2 {
        width: 20px;
        height: 20px;
    }

    &.x3 {
        width: 30px;
        height: 30px;
    }

    &.x4 {
        width: 40px;
        height: 40px;
    }

    &.x5 {
        width: 50px;
        height: 50px;
    }

    &.x6 {
        width: 60px;
        height: 60px;
    }

    &.x7 {
        width: 70px;
        height: 70px;
    }

    &.x8 {
        width: 80px;
        height: 80px;
    }

    &.x8 {
        width: 80px;
        height: 80px;
    }

    &.x10 {
        width: 100px;
        height: 100px;
    }
}
$icons: arrow-down arrow-right arrow-left-circle circle forum glasses heart like schedule share shopping-bag stop user volume envelope options eye forum-featured forum-lock forum-topic forum-topic-posts like-filled arrow-left-circle-2 arrow-right-circle-2 circle-yellow;
@each $icon in $icons {
    .icn {
        &.#{$icon} {
            background-color: #FFF;
            -webkit-mask: url('../images/icons/#{$icon}.svg') no-repeat center center;
                    mask: url('../images/icons/#{$icon}.svg') no-repeat center center;
                    -webkit-mask-size: contain;
                    mask-size: contain;
        }
    }
}
