 *
 * Structure
 */

/* Container structure */
.body-container {
    background-image: url('../images/bg.svg');
    background-attachment: fixed;
    background-position: center 130%;
    background-repeat: no-repeat;
    padding: 0;
    min-height: 100%;
    width: 100%;
}

.container-user-area {
    padding: 40px 80px 0;
    @media (max-width: 1199.98px) {
        padding: 20px 40px 0;
    }
}

.container-welcome-player {
    margin-top: 110px;
    padding: 0 120px;
    @media (max-width: 1199.98px) {
        padding: 0 60px;
    }
}

.container-navigation-wrapper {
    @media (min-width: 1200px) {
        position: absolute;
        bottom: 60px;
    }
    @media (max-width: 1199.98px) {
        position: static;
    }
}

.container-navigation {
    padding: 0 80px;
    @media (max-width: 1199.98px) {
        padding: 0 40px;
    }
}

.container-navigation-menu {
    padding: 0 0 0 80px;
    @media (max-width: 1199.98px) {
        padding: 0 0 0 40px;
    }
}

/* Page structure */
.container-page-content {
    padding: 0 40px;
    @media (max-width: 1199.98px) {
        padding: 0 20px;
    }
}

.page-sidebar {
    margin-top: -25px;
    @media (min-width: 992px) {
        position: fixed;
    }
}

.page-main-content {
    @media (min-width: 992px) {
        padding-left: 40px;
    }
}

.page-content-scroll {
    padding-bottom: 150px;
}

.title-area {
    font-weight: $fontBold;
    font-size: 4rem;
    color: #fff;
}

.description-area {
    font-size: 2.5rem;
    color: #FFF;
    font-weight: $fontLight;
    height: 50%;
    position: relative;

    &:after {
        background:    -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(145,175,241,1) 50%, rgba(255,255,255,0) 100%);
        background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        background:         linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        position: absolute;
        top: 0;
        right: 0;
        width: 4px;
        height: 100%;
        content: '';
        opacity: 0.2;
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=0 );
    }
}

.subtitle-area {
    font-size: 1.6rem;
    color: #fff;
    position: relative;

    .ntf {
        border-radius: 100%;
        background-color: $blue;
        font-weight: $fontBold;
        font-size: 1.2rem;
        color: #fff;
        float: left;
        margin-top: 7px;
        margin-left: 7px;
        width: 32px;
        height: 32px;
        text-align: center;
    }
}

.subtitle-desc {
    color: #9DAFD6;
    font-size: 1.1rem;
}

.list-options {
    position: relative;
    margin-top: 70px;
    &:after {
        background:    -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(145,175,241,1) 50%, rgba(255,255,255,0) 100%);
        background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        background:         linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        position: absolute;
        top: 0;
        right: 0;
        width: 4px;
        height: 100%;
        content: '';
        opacity: 0.2;
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=0 );
    }

    .option {
        font-weight: $fontLight;
        font-size: 2rem;
        color: #8291b1;
        position: relative;
        margin-left: -40px;
        padding: 3px 0 3px 40px;
        cursor: pointer;

        @include transition(0.3s all);

        &:hover,
        &.active {
            background-color: $blue;
            color: #fff;
            padding-left: 50px;

            @include transition(0.3s all);

            .ntf {
                background-color: #fff;
                color: $blue;
            }

            .icn {
                background-color: #fff;
                @include transition(0.3s all);
            }
        }

        &.white {
            color: #fff;

            .icn {
                background-color: #fff;
            }
        }

        &.yellow {
            color: #FFDD00;

            .icn {
                background-color: #FFDD00;
            }
        }

        .icn {
            background-color: #8291b1;
            @include transition(0.3s all);
        }

        .ntf {
            border-radius: 100%;
            background-color: rgba(#414f90, 0.5);
            font-weight: $fontBold;
            font-size: 1.2rem;
            color: #fff;
            position: absolute;
            top: (54px - 32px) / 2;
            right: 15px;
            width: 32px;
            height: 32px;
            text-align: center;
        }
    }
}

.back-main-page {
    font-size: 1.5rem;
    color: #fff;
    position: fixed;
    bottom: 120px + 30px;
    z-index: 10;
    width: 250px;
    opacity: 0.85;
    cursor: pointer;

    @include transition(0.3s all);

    @media (max-width: 991.98px) {
        position: static;
    }

    &:hover {
        opacity: 1;
        color: #FFDD00;

        @include transition(0.3s all);

        .icn {
            background-color: #FFDD00;
            @include transition(0.3s all);
        }
    }

    .icn {
        float: left;
        @include transition(0.3s all);
        margin-top: 5px;
        margin-right: 15px;
    }
}

/* Player on bottom */
.player-bottom {
    background-color: rgba($darkblue2, 0.9);
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 10000;
    min-height: 100px;
    width: 100%;

    .container-player-bottom {
        padding: 0 40px;
    }
    .habbo-image {
        margin-top: -60px;
        width: 128px;
        height: 100px + 60px;
    }

    .player-program {
        font-size: 1.5rem;
        color: #fff;
    }

    .player-broadcaster {
        font-size: 1.2rem;
        color: $blue;
    }

    .player-current-hotel {
        background-color: #2f4176;

        .icn {
            position: relative;
            top: 2px;
        }
    }

    .player-listeners {
        font-weight: $fontSemilight;
        font-size: 1.6rem;
        color: #fff;
        @media (max-width: 1290px) {
            font-size: 1.5rem;
        }
    }

    .player-volume {
        font-size: 1.6rem;
        color: #fff;
        @media (max-width: 1290px) {
            font-size: 1.5rem;
        }
    }
}

/* Navigation effect */
.navigation-wrapper-effect {
    display: none;
}

.navigation-effect-background {
    background: $darkblue;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    width: 100%;
    height: 100%;
}
.navigation-effect-bar-1 {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000 + 1;
    width: 0;
    height: 100%;

    @include gradient($darkblue, darken($darkblue, 10%));
}

.navigation-effect-bar-2 {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000 + 1;
    width: 0;
    height: 100%;

    @include gradient($lightblue, darken($lightblue, 10%));
}

/* Paginate */
.paginate-content-wrapper {
    text-align: center;

    @media (min-width: 768px) {
        text-align: left;
    }
}

.paginate-col-buttons {
    text-align: center;

    @media (min-width: 768px) {
        text-align: right;
    }
}

.paginate-text {
    color: #FFF;
    font-size: 1.4rem;
}

.paginate-left, .paginate-right {
    position: relative;
    top: 7px;
    @include transition(0.3s all);

    &:hover:not(.inactive) {
        background-color: #FFDD00;
        cursor: pointer;
        @include transition(0.3s all);
    }

    &.inactive {
        opacity: 0.5;
    }
}

.paginate-left {
    margin-left: 10px;
}

.paginate-right {
    margin-right: 10px;
}
