.box-topic-entry {
    border-radius: 6px;
    background-color: $lightblue2;
    position: relative;
    padding: 20px;
    width: 100%;

    &:not(.reply) {
        &:before {
            position: absolute;
            top: -12px;
            left: 50px;
            content: '';

            @include triangle(24px, $lightblue2, up);
        }
    }

    .author-infos {
        &:after {
            background:    -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(145,175,241,1) 50%, rgba(255,255,255,0) 100%);
            background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
            background:         linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
            position: absolute;
            top: 0;
            right: 0;
            width: 4px;
            height: 100%;
            content: '';
            opacity: 0.2;
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=0 );
        }
    }

    .author-username {
        font-size: 1.5rem;
        color: #fff;
    }

    .habbo-image {
        background-position: center center;
        background-repeat: no-repeat;
        width: 100%;
        height: 220px;
    }

    .ranking {
        border: 2px solid #ff01f7;
        border-radius: 6px;
        font-size: 1.1rem;
        color: #fff;
        padding: 10px 20px;
        &.pink {
            box-shadow: 0 0 20px 20px #6b5aa5 inset;
        }
    }

    .author-avatar {
        border-radius: 100%;
        display: inline-block;
        width: 40px;
        height: 40px;

        @include avatar-properties;
    }

    .author-published {
        font-size: 1.1rem;
        color: #fff;
        position: relative;
        top: -13px;
        display: inline-block;
        margin-left: 10px;
    }

    .box-topic-option {
        font-size: 1rem;
    }

    .topic-content {
        font-size: 1rem;
        color: #d2e0ff;
        padding: 20px;
    }
}
