 * NextHabbo - by Henrique Arthur <eu@henriquearthur.com.br>
 *
 * Main page
 */

/* Header - User area */
.user-logged-area {
    float: right;

    .username {
        font-size: 1rem;
        color: #fff;
        float: left;
        padding-top: 15px;
    }

    .icon {
        border-radius: 100%;
        background-color: #1d1e5c;
        font-size: 1.3rem;
        line-height: 58px;
        color: #fff;
        display: inline-block;
        margin-left: 10px;
        width: 60px;
        height: 60px;
        text-align: center;
    }
}

.welcome-area {
    color: #fff;

    h1 {
        font-weight: $fontBold;
        font-size: 3.7rem;
    }

    h2 {
        font-weight: $fontLight;
        font-size: 2.1rem;
    }
    @media (max-width: 1290px) {
        h1 {
            font-size: 2rem;
        }

        h2 {
            font-size: 1.5rem;
        }
    }
}
/* Header - Player */
.player-area {
    .habbo-image-wrapper {
        background-image: url('../images/user-podium.png');
        background-position: bottom center;
        background-repeat: no-repeat;
        width: 100%;
        height: 240px;
    }

    .habbo-image {
        position: relative;
        margin: 0 auto;
        width: 128px;
        height: 220px;
    }

    .player-title {
        font-size: 1.7rem;
        color: #fff;
        @media (max-width: 1290px) {
            font-size: 1.5rem;
        }
    }

    .player-current-hotel {
        .icn {
            position: relative;
            top: 2px;
        }
    }

    .player-listeners {
        font-weight: $fontSemilight;
        font-size: 1.6rem;
        color: #fff;
        @media (max-width: 1290px) {
            font-size: 1.5rem;
        }
    }

    .player-volume {
        font-size: 1.6rem;
        color: #fff;
        @media (max-width: 1290px) {
            font-size: 1.5rem;
        }
    }

    .player-program {
        font-weight: $fontBold;
        font-size: 2.3rem;
        color: #fff;
        width: 100%;
        @media (max-width: 1290px) {
            font-size: 1.8rem;
        }
    }

    .player-infos {
        font-size: 1.2rem;
        color: #fff;
        width: 100%;
    }

    .player-button-col {
        padding-right: 10px;
        padding-left: 10px;
    }

    .player-button {
        font-size: 2.5rem;
        padding: 4px 0 0;
        width: 100%;
        height: 120px;
        @media (max-width: 1700px) {
            font-size: 1.8rem;
            height: 100px;
        }
        @media (max-width: 1500px) {
            font-size: 1.3rem;
            height: 85px;
        }
        @media (max-width: 1199.98px) {
            height: 75px;
        }
        @media (max-width: 650px) {
            height: 90px;
        }
        @media (max-width: 575.98px) {
            height: 90px;
        }
        @media (max-width: 350px) {
            height: 70px;
        }
    }
}

.player-volume-dropdown {
    border: 1px solid $blue;
    border-radius: 6px;
    background-color: #34589d;
    box-shadow: 0 3px 6px rgba(#000, 0.16);

    .slider-border {
        border-radius: 100px;
        background: rgba(0, 0, 0, 0.3);
        margin: 0 auto;
        width: 96px;
        height: 6px;
    }

    .slider {
        border: 0;
        background: none;
        position: relative;
        width: 85px;
        height: 6px;
    }

    .ui-slider-handle {
        border: 0;
        border-radius: 100px;
        background-color: #fff;
        box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.3);
        position: absolute;
        top: -3px;
        margin-left: 0;
        width: 12px;
        height: 12px;
        outline: none;
    }

    .ui-slider-range {
        border-radius: 100px;
        background: rgba(0, 0, 0, 0.2);
        height: 6px;
    }
}
/* Header - Navigation */
.navigation-title {
    font-size: 2rem;
    color: #fff;
}

.navigation-helper {
    font-size: 1.2rem;
    color: #fff;
}

.navigation-menu {
    .slick-slide {
        outline: none !important;
    }
}

.navigation-box {
    padding-top: 20px;
    width: 100%;
    cursor: pointer;

    &:hover {
        .icon {
            border-color: $blue;
            box-shadow: 0 0 30px 30px rgba(#fff, 0.05) inset;

            @include transition(0.3s all);
        }
    }

    &.active,
    &:active {
        .icon {
            border-color: $blue;
            background: $blue;

            @include transition(0.3s all);
        }

        .name {
            color: #fff;
        }
    }

    .icon {
        border: 2px solid rgba($lightblue2, 0.9);
        border-radius: 6px;
        background-color: rgba($lightblue2, 0.9);
        font-size: 5rem;
        line-height: 150px;
        color: #fff;
        position: relative;
        width: 100%;
        height: 160px;
        text-align: center;

        @include transition(0.3s all);
    }

    .notification {
        border-radius: 100%;
        background-color: #ff4a4a;
        font-weight: $fontBold;
        font-size: 1.2rem;
        line-height: 39px;
        position: absolute;
        top: -20px;
        right: -20px;
        width: 40px;
        height: 40px;
        letter-spacing: -1px;
    }

    .name {
        font-size: 1.1rem;
        color: #8291b1;
    }
}

/* Forum */
.box-topic {
    border: 2px solid rgba($lightblue, 0.8);
    border-radius: 6px;
    background-color: rgba($lightblue, 0.8);
    min-height: 120px;
    width: 100%;

    @include transition(0.3s all);

    &:hover:not(.featured),
    &.active {
        border-color: $blue;
        box-shadow: 0 0 50px 50px rgba(#fff, 0.05) inset;

        @include transition(0.3s all);
    }

    &.featured {
        box-shadow: 0 3px 6px rgba(#000, 0.16), 0 0 0 2px #fd0, 0 0 50px 50px rgba(#fff, 0.05) inset;

        .author {
            color: #fd0;
        }
    }

    > .row {
        z-index: 2;
        min-height: 120px;
    }

    .icn.forum-featured {
        background-color: #fd0;
    }

    .title {
        font-size: 1.5rem;
        color: #fff;

        @media (max-width: 767.98px) {
            font-size: 1rem;
        }
    }

    .author {
        font-size: 1.2rem;
        color: #9dafd6;

        @media (max-width: 767.98px) {
            font-size: 0.9rem;
        }
    }

    .stats-number {
        font-size: 1.4rem;
        color: #fff;
        position: relative;
        top: -11px;
        display: inline-block;
        margin-left: 5px;
    }

    .user-avatar {
        border-radius: 100%;
        float: right;
        width: 60px;
        height: 60px;

        @include avatar-properties;

        @media (max-width: 767.98px) {
            float: none;
        }
    }
}

/* Hotels */
.box-hotel-partner {
    width: 100%;

    &:hover {
        .image {
            box-shadow: 0 0 0 2px $blue, 0 3px 6px rgba(#000, 0.3);

            @include transition(0.3s all);
        }

        .description {
            color: $blue;

            @include transition(0.3s all);
        }
    }

    .image {
        border-radius: 6px;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 200px;

        @include transition(0.3s all);
    }

    .infos-blur {
        border-radius: 6px;
        background-position: center 140px;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 99;
        width: 100%;
        height: 60px;

        @include filter(blur(10px));
    }

    .infos {
        border-bottom-right-radius: 6px;
        border-bottom-left-radius: 6px;
        background-color: rgba($darkblue, 0.6);
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 100;
        width: 100%;
        height: 60px;
    }

    .users {
        font-size: 1.5rem;
        color: #fff;
    }

    .title {
        font-size: 1.2rem;
        color: #fff;
    }

    .description {
        font-size: 1.1rem;
        color: #9dafd6;

        @include transition(0.3s all);
    }
}

/* Articles */
.box-article {
    &:hover {
        .image {
            box-shadow: 0 0 0 2px $blue, 0 3px 6px rgba(#000, 0.3), 0 0 100px 10px rgba(#000, 0.6) inset;

            @include transition(0.3s all);
        }

        .title {
            color: #fff;

            @include transition(0.3s all);
        }

        .category {
            color: $blue;

            @include transition(0.3s all);
        }
    }

    .image {
        border-radius: 6px;
        background-size: cover;
        background-repeat: no-repeat;
        box-shadow: 0 0 100px 10px rgba(#000, 0.3) inset;
        position: relative;
        width: 100%;
        height: 160px;

        @include transition(0.3s all);

        .caption {
            border-radius: 6px;
            width: 100%;
            height: 160px;

            @include gradient(rgba(#000, 0), rgba(#000, 0.3));
        }
    }

    .title {
        font-size: 1.2rem;
        color: #8291b1;
        margin-top: 8px;

        @include transition(0.3s all);
    }

    .category {
        font-size: 1.1rem;
        color: #8291b1;
        margin-top: 8px;

        @include transition(0.3s all);
    }
}

/* Gallery */
.box-gallery {
    border: 2px solid rgba($lightblue, 0.8);
    border-radius: 6px;
    background-color: rgba($lightblue, 0.8);
    padding: 8px;

    @include transition(0.3s all);

    &:hover {
        box-shadow: 0 0 0 2px $blue, 0 3px 6px rgba(#000, 0.3), 0 0 50px 50px rgba(#FFF, 0.05) inset;;

        @include transition(0.3s all);
    }

    .image {
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 320px;

        @include transition(0.3s all);
    }

    .infos-blur {
        border-radius: 6px;
        background-position: center (320px - 60px);
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 99;
        width: 100%;
        height: 60px;

        @include filter(blur(10px));
    }

    .infos {
        background-color: rgba($darkblue, 0.6);
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 100;
        width: 100%;
        height: 60px;
    }

    .users {
        font-size: 1.5rem;
        color: #fff;
    }

    .avatar {
        border: 1px solid $blue;
        border-radius: 100%;
        padding: 2px;
        width: 65px;
        height: 65px;
    }

    .avatar-image {
        border-radius: 100%;
        width: 65px - 2px - 2px - 1px - 1px;
        height: 65px - 2px - 2px - 1px - 1px;

        @include avatar-properties;
    }

    .author {
        font-size: 1.2rem;
        color: #fff;
    }
}

/* Events */
.box-event {
    width: 100%;

    &:hover,
    &.active {
        .image {
            box-shadow: 0 0 0 2px $blue, 0 3px 6px rgba(#000, 0.3);

            @include transition(0.3s all);
        }

        .info-title {
            color: #fd0;

            @include transition(0.3s all);
        }
    }

    .image {
        border-radius: 6px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        height: 100px;

        @include transition(0.3s all);
    }

    .info-title {
        font-size: 1.1rem;
        color: #9dafd6;

        @include transition(0.3s all);
    }

    .info-description {
        font-size: 1.3rem;
        color: #fff;
    }

    .border-radial {
        background:    -moz-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(145,175,241,1) 50%, rgba(255,255,255,0) 100%);
        background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        background:         linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(145,175,241,1) 50%,rgba(255,255,255,0) 100%);
        width: 100%;
        height: 4px;
        opacity: 0.2;
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=1 );
    }
}

/* Market */
.box-market-item {
    width: 100%;

    &:hover {
        .price {
            color: #fd0;

            @include transition(0.3s all);
        }

        .icn {
            background-color: #FFDD00;
        }
    }

    .image-wrapper {
        border-radius: 6px;
        width: 100%;
        height: 160px;

        background-color: rgba($lightblue, 0.8);
    }

    .image {
        background-position: center center;
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
    }

    .price {
        font-size: 1.1rem;
        color: #fff;
        margin-top: 10px;

        @include transition(0.3s all);

        .icn {
            position: relative;
            top: 4px;
        }
    }
}
